.App {
    text-align: center;
  }
  
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }


.Footer{
    background-color:rgb(77, 204, 243);
    color:white;
    padding:10px;
    width:100%;
    height:50px;
    position: relative;
  }
  
  .Footer p{
    margin-top: 10px;
  }